import * as React from 'react';
import { Container, CssBaseline, Typography, Grid, TextField, Stack, Box } from '@mui/material';
import { styled } from '@mui/material/styles';
import Button from '@mui/material/Button';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import NavBar from '../components/NavBar'

const Input = styled('input')({
  display: 'none',
});

export default function Update() {
  
  return (
    <div>
      <NavBar></NavBar>
      <CssBaseline />
      <Container maxwidth="lg" sx={{ mt : 13 }}>
        <h2>  </h2>
        <Grid container>
          <Grid item xs={3} >
            <AccountCircleIcon sx={{ fontSize: 150 }}/>
            <label htmlFor="upload-button">
              <Input accept="image/*" id="upload-button" multiple type="file" />
              <Button
                variant="contained"
                component="span"
                sx={{ mt : 3 }}
              >
                  
              </Button>
            </label>
          </Grid>
          <Grid item xs={9}>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography></Typography>
              <Typography sx={{ ml : '13ch', fontWeight: 'bold', mb : 2}}> </Typography>
            </Stack>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography sx={{ mr : 2, width : 150 }}></Typography>
              <TextField sx={{ width : '50ch' }} multiline />
              <Typography sx={{ m : 2}}>0 / 150</Typography>
            </Stack>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography sx={{ mr : 2, width : 150 }}></Typography>
              <TextField sx={{ width : '50ch' }}/>
            </Stack>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography sx={{ mr : 2, width : 150 }}> </Typography>
              <TextField sx={{ width : '50ch' }}/>
              <Button variant="contained" sx={{ ml : 2}}> </Button>
            </Stack>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography sx={{ mr : 2, width : 150 }}></Typography>
              <TextField sx={{ width : '50ch' }}/>
              <Button variant="contained" sx={{ ml : 2, width : '16ch' }}> </Button>
            </Stack>
            <Stack direction="row" sx={{ mt : 2}}>
              <Typography ></Typography>
              <Typography sx={{ ml : '13ch', fontWeight: 'bold', mb : 2}}> </Typography>
            </Stack>
          </Grid>
        </Grid>
        <Box sx={{ justifyContent: "center" }}>
          <Button variant="contained" > </Button>
        </Box>
      </Container>
    </div>
  )
}